import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

import { Button } from 'react-bootstrap'

import List from './List.js'
import Modal from './Modal.js'


export class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: "",
      modalVisible: false,
      newName:"",
      newIngredient: "",
      recipeList: []
    }
    
    this.toggleModal = this.toggleModal.bind(this);
    this.handleInput = this.handleInput.bind(this);
    this.addRecipe = this.addRecipe.bind(this);
    this.edit = this.edit.bind(this);
    this.remove = this.remove.bind(this);
  }
  
  componentWillMount() {
    let startList;
    if (!JSON.parse(localStorage.getItem('recipes'))) {
      startList = JSON.parse(localStorage.getItem('recipes'));
    } else {
      startList = [
        {
          id: 1,
          name: "Bread",
          ingredients: ["Water", "Flour", "Yeast", "Salt"],
          directions: ["Mix the ingredients", "heat the oven", "put the dough in the oven", "wait 30 minutes"]
        },
        {
          id: 2,
          name: "Grilled PB&J Sandwich",
          ingredients: ["Bread", "Butter", "Peanut Butter", "Jelly"],
          directions: ["Put peanut butter on the bread", "Put Jelly on the bread", "put the bread together", "enjoy!"]
        }
      ]
      
      localStorage.setItem("recipes", JSON.stringify(startList));
    }
    
    this.setState({
      recipeList: startList
    });
  }

  
  toggleModal() {
    this.setState({
      modalVisible: !this.state.modalVisible,
      id: '',
      newName: '',
      newIngredient: '',
      newDirections: ''
    });
  }
  
  
  handleInput(e, inputType) {
    this.setState({
      [inputType]: e.target.value
    });
  }
  
  
  edit(editId) {
    this.toggleModal();
    let editItem = this.state.recipeList.filter((val) => {
      return val.id == editId
    });
    
    this.setState({
      id: editItem[0].id,
      newName: editItem[0].name,
      newIngredient: editItem[0].ingredients.join(),
      newDirections: editItem[0].directions.join()
    });
  }
  
  
  addRecipe() {
    let newList = this.state.recipeList.slice();
    let addId;
    if (this.state.id) {
      addId = this.state.id
    } else {
      addId = newList.length + 1
    }
    
    let newItem = {
      id: addId,
      name: this.state.newName,
      ingredients: this.state.newIngredient.split(","),
      directions: this.state.newDirections.split(",")
    }
    
    const spellCheck = (val) => {
      if (val[val.length - 1] == false) {
        val.splice(val.length - 1, 1);
      }
    }
    
    spellCheck(newItem.ingredients);
    spellCheck(newItem.directions);
    
    for (let val in newItem) {
      if (newItem[val] == false) {
        alert("Hey Pal, one or more of your fields is blank.");
        return false;
      }
    }
    
    newList.splice(addId - 1, 1, newItem);

    this.setState({
      recipeList: newList
    });
    
    localStorage.setItem("recipes", JSON.stringify(newList));
    this.toggleModal();
  }

  
  remove(deleteItem) {
    let newList = this.state.recipeList.filter((val) => {
      return val.id != deleteItem
    });
    
    this.setState({
      recipeList: newList
    });
    
    localStorage.setItem("recipes", JSON.stringify(newList));
  }
 

  render() {
    return (
      <div>
        {
          this.state.modalVisible && 
          <Modal 
            toggleModal={this.toggleModal}
            handleInput={this.handleInput}
            addRecipe={this.addRecipe}
            newName={this.state.newName}
            newIngredient={this.state.newIngredient}
            newDirections={this.state.newDirections}
          />
        }
        
        <List 
          recipeList={this.state.recipeList}
          edit={this.edit}
          remove={this.remove}
        />
        
        <Button id='add' onClick={this.toggleModal}>Add New Recipe <i className='fa fa-plus'></i></Button>
      </div>
    );
  }
}

ReactDOM.render(<App />, document.getElementById("box"));



