import React from 'react';

import { Button, Accordion, Panel, ButtonGroup, Col } from 'react-bootstrap'

const List = (props) => {
  let list = props.recipeList.map((val, i) => {
    return (
      <Panel header={val.name} eventKey={i}>
        
        <Col sm={12} md={4}className='list'>
          {val.ingredients.map((listItems) => {
            return <div className='list-item'>{listItems}</div>
          })}
        </Col>

        <Col sm={12} md={8} className='direction'>
          <p>Instructions</p>
          <ol>
            {val.directions.map((listItems) => {
              return <li className='step-item'>{listItems}</li>
            })}
          </ol>
        </Col>
        
        <ButtonGroup justified>
          <Button 
            href="#" 
            bsStyle='warning' 
            onClick={() => {props.edit(val.id)}}>
              Edit <i className='fa fa-edit' />
          </Button>
          
          <Button 
            href="#" 
            bsStyle='danger' 
            onClick={() => {props.remove(val.id)}}>
              Delete <i className='fa fa-remove' />
          </Button>
        </ButtonGroup>
        
      </Panel>
    )
  });
  
  return <Accordion>{list}</Accordion>
};

export default List;


