import React from 'react';
import { Button } from 'react-bootstrap';


const Modal = (props) => {
    return (
      <div>
        <div id='dimmer' onClick={props.toggleModal} />
        <div id='modal'>
          <h4>Name</h4>
          <input 
            placeholder="Your recipe name"
            onChange={() => {props.handleInput(window.event.target.value, 'newName')}}
            value={props.newName}
          />
          
          <hr />
          
          <h4>Ingredients</h4>
          <textarea 
            placeholder="Your ingredients separated by commas"
            onChange={() => {props.handleInput(window.event.target.value, 'newIngredient')}}
            value={props.newIngredient}
          />
          
          <hr />
          
          <h4>Instructions</h4>
          <textarea
            placeholder="Each step separated by commas"
            onChange= {() => {props.handleInput(window.event.target.value, 'newDirections')}}
            value={props.newDirections}
          />
          
          <hr />
          
          <Button onClick={props.addRecipe} bsStyle='success' block>Submit Recipe</Button>
        </div>
      </div>
    );
};

export default Modal



